import React from "react";
import Helmet from "react-helmet";
import parse from "html-react-parser";

export default ({ seoMaticData }) => {
  if(seoMaticData === undefined) return null;

  let headHTML = ""

  Object.keys(seoMaticData).forEach(key => {
    headHTML += seoMaticData[key];
  });

  return (
    <Helmet> 
      { parse(headHTML) }
    </Helmet>
  )
}